
import { Options, Vue } from "vue-class-component";
import { createApp } from "vue";

enum Level {
  INFO,
  WARNING,
  SUCCESS,
}

@Options({})
export default class NotifyComponent extends Vue {
  isShow = false;
  message = "";
  level = 1;
}

export class Notify {
  private static notify(msg: string, level: Level): void {
    let div = document.createElement("div") as HTMLDivElement;
    if (div) {
      div.remove();
      window.clearTimeout();
    }
    div = document.createElement("div") as HTMLDivElement;
    div.id = "notify";

    document.body.appendChild(div);
    let notify = createApp(NotifyComponent).mount("#notify") as NotifyComponent;
    notify.message = msg;
    notify.isShow = true;
    notify.level = level;
    setTimeout(() => {
      notify.isShow = false;
    }, 2000);
    setTimeout(() => {
      div.remove();
      window.clearTimeout();
    }, 3000);
  }
  static info(msg: string) {
    this.notify(msg, Level.INFO);
  }
  static warning(msg: string) {
    this.notify(msg, Level.WARNING);
  }
  static success(msg: string) {
    this.notify(msg, Level.SUCCESS);
  }
}
